import React, { Component } from 'react';
import Navitem from './Navitem';
import $ from 'jquery';
class Navbar extends Component {
    constructor(props)
    {
        super(props);
        this.state={
            'NavItemActive':''
        }
    }
    activeitem=(x)=>
    {
        if(this.state.NavItemActive.length>0){
            document.getElementById(this.state.NavItemActive).classList.remove('active');
        }
        this.setState({'NavItemActive':x},()=>{
            document.getElementById(this.state.NavItemActive).classList.add('active');
        });
    };
    handleToggle = () => {
        $(".menu-icon").on("click", function () {
            $("nav ul").toggleClass("showing");
        });
    };
    render() {
        return (
            <nav>
            <div className="menu-icon" data-toggle="collapse" >
                <i className="fa fa-bars fa-2x" onClick={this.handleToggle} ></i>
            </div>
            <div className="logo">
                <a link href="/">S</a>
            </div>
            <div className="menu">
                <ul className="navbar">
                <Navitem item="Home" tolink="/"  activec={this.activeitem}></Navitem>
                <Navitem item="About" tolink="/about"  activec={this.activeitem}></Navitem>
                <Navitem item="Work" tolink="/work"  activec={this.activeitem}></Navitem>
                <Navitem item="Photography" tolink="/photography"  activec={this.activeitem}></Navitem>
                </ul>
            </div>
        </nav>
            )
        }
    }
    
    export default Navbar